/***************************************************************

.c-hamburger-btn

***************************************************************/
.c-hamburger-btn {
  padding: 0;
  width: 1.625rem;
  height: 1.25rem;
  cursor: pointer;
  position: relative;
  transition: all 0.3s;
}

.c-hamburger-btn span {
  display: inline-block;
  width: 100%;
  height: 0.15625rem;
  background-color: #fff;
  border-radius: 0.0625rem;
  position: absolute;
  transition: all 0.3s;
}

.c-hamburger-btn span:nth-child(1) {
  top: 0;
  left: 0;
}

.c-hamburger-btn span:nth-child(2) {
  top: 0.5625rem;
  left: 0;
}

.c-hamburger-btn span:nth-child(3) {
  bottom: 0;
  left: 0;
}

.c-hamburger-btn.is-open {
  transform: rotate(-180deg);
}

.c-hamburger-btn.is-open span:nth-child(1) {
  transform: translateY(0.55625rem) rotate(-135deg);
}

.c-hamburger-btn.is-open span:nth-child(2) {
  transform: translateY(0) rotate(-45deg);
}

.c-hamburger-btn.is-open span:nth-child(3) {
  opacity: 0;
}

