  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: #333333;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: -500;
  transform-origin: right;
  transform: scale(0, 1);
  transition: transform 0.3s;
}

